@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}



@media (prefers-reduced-motion: no-preference) {

}

.App-header {
  background: rgb(197,84,245);
  background: radial-gradient(circle, rgba(197,84,245,1) -31%, rgba(29,22,82,0.9360119047619048) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.boton {
    padding: 10px;
    font-size: 20px;
    border-radius: 20px;
    border: 0;
    margin: 10px 0;
    background-color:  #3fbda8;
    color:#fff;
    width: 100%;
    outline:none;
    cursor: pointer;
}
.boton:hover{
    background-color:  #2a9e8b;
}
.contador-container{
    padding: 20px;
    border-radius: 10px;
    min-width: 150px;
     background: rgba(29,22,82,0.3);
    box-shadow: 7px 7px 5px -2px rgba(0,0,0,0.4);
}
.main-title{
    font-size: 50px;
}
